.languageSwitcher{display:flex;align-items:center;gap:.5rem;font-size:.75rem;padding:.5rem;transition:all .6s cubic-bezier(0.25, 0.46, 0.45, 0.94)}.languageSwitcher:hover{border-color:var(--color-green-hover)}.langButton{background:none;border:none;color:#000;cursor:pointer;padding:.25rem .5rem;transition:all .6s cubic-bezier(0.25, 0.46, 0.45, 0.94);font-size:.75rem;font-weight:300;text-transform:uppercase;letter-spacing:.05em}.langButton:hover{transform:scale(1.1)}.langButton.active{color:var(--color-green);font-weight:600;transform:scale(1.05)}.separator{color:var(--color-green-hover);font-weight:300;font-size:.8rem}